import React, { Component } from "react";
import PropTypes from "prop-types";
import isEmpty from"../../validation/is-empty";

class ProfileAbout extends Component {
  render() {
    const { profile } = this.props;

    // Get first name:
    // Trim off spaces, then split from the space in the middle, turning the first and last name into an array of 2 elements and pulling the first element(first name)
    const firstName =profile.user.name.trim().split(" ")[0];
    
    // Skill List:
    const skills = profile.skills.map((skill, index) => (
      <div key={index} className="p-3">
        <i className="fa fa-check"> {skill} </i>
      </div>
  ));

    return (
      <div className="row">
        <div className="col-md-12">
          <div className="card card-body bg-light mb-3">
            <h3 className="text-center text-info">{firstName}'s Bio</h3>
    <p className="lead">{isEmpty(profile.bio) ? (<span>{firstName} Does Not Have A Bio</span>) : (<span>{profile.bio}</span>)}
            </p>
            <hr />
            <h3 className="text-center text-info">Skill Set</h3>
            <div className="row">
              <div className="d-flex flex-wrap justify-content-center align-items-center">
                {skills}
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

ProfileAbout.propType ={
  profile: PropTypes.object.isRequired
}

export default ProfileAbout;



